/*
 *  Object fit mixin
 *
 *  Dependencies:

 *
 *  Version:
 *    1.0.0 - 2018/01/24
 */

 @mixin t-object-fit($fit: fill, $position: null){
  
  -o-object-fit: $fit;
     object-fit: $fit;
  @if $position {
    -o-object-position: $position;
       object-position: $position;
    font-family: 'object-fit: #{$fit}; object-position: #{$position}';
  } @else {
    font-family: 'object-fit: #{$fit}';
  }
  @supports (object-fit: $fit){
    height: 100%;
  }
}