/*
 *  Home Hero
 *    Styles for the hero  section on the home page.
 *
 *
 *  Version:
 *    1.0.0 - 2020/07/14
 */

@import '../base';

.c-home-hero {
  position: relative;
  color: $s-color--white;

  &.is-hero-science {
    .c-home-hero__container {
      padding-bottom: 115%;
    }
  }

  &__image {
    width: 100%;
    position: absolute;
    @include t-object-fit(cover, top);

    &-container {

      &.is-desktop {
        display: none;
      }
    }
  }

  &__container {
    position: relative;
    padding-bottom: 183%;

    .is-hero-landing-page & {
      padding: 0;
      height: 100%;
      width: 100%;
      position: absolute;
    }
  }

  &__video {
    height: 100%;
    width: 100%;
    position: absolute;
    object-fit: cover;

    &.is-desktop {
      display: none;
    }

    &.is-mobile {
      display: block;
    }
  }

  &__scroll {
    width: 2.375rem;
    height: 2.375rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    border-radius: 50%;
    background: $s-color--white;
    color: $s-color--blue-green;
    cursor: pointer;
    @include t-anim--transition(all);

    svg {
      width: 0.4375rem;
    }

    &:hover {
      background: $s-color--blue-green;
      color: $s-color--white;
    }
  }

  &__title {
    max-width: t-to-rem(350);
    margin-bottom: 1rem;
    color: $s-color--font-6;

    .is-dark-mode & {
      color: $s-color--font-1;
    }
  }

  &__subtitle {
    @include t-font--type-5;
    max-width: t-to-rem(330);
    margin-bottom: t-to-rem(30);
    margin-top: 0;
    color: $s-color--font-6;

    .is-dark-mode & {
      color: $s-color--font-1;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__title {
      &.o-heading--1 {
        font-size: t-to-rem(166);
      }
    }
    &__subtitle {
      font-size: t-to-rem(22);
    }
  }
  @include t-media-query($s-medium-down) {
    &__title {
      &.o-heading--1 {
        font-size: t-to-rem(75);
      }
    }
    &__subtitle {
      font-size: t-to-rem(18);
    }
  }

  &__caveat {
    margin-top: 1rem;
    color: $s-color--font-6;

    .is-dark-mode & {
      color: $s-color--font-1;
    }
  }

  &__content {
    position: absolute;
    width: 100%;
    top: 0;
    @include t-absolute-center-x;
    margin-top: t-to-rem(122);
    display: flex;
    flex-direction: column;
    color: $s-color--font-6;
    @include t-gutters(true);

    .is-hero-landing-page & {
      position: relative;
      padding-top: t-to-rem(122);
      padding-bottom: t-to-rem(62);
      margin-top: 0;
    }

    &.is-position {

      &-middle,
      &-bottom {
        &-left {
          text-align: left;
          align-items: flex-start;
        }

        &-center {
          text-align: center;
          align-items: center;
        }

        &-right {
          text-align: right;
          align-items: flex-end;
        }
      }
    }
  }

  /*==== Media Queries ====*/
  @media only screen and (min-width: 34.375rem) {
    &.is-hero-science {
      .c-home-hero__container {
        padding-bottom: 70%;
      }
    }

    &__container {
      padding-bottom: 114%;
    }
  }

  @include t-media-query($s-small-up) {
    &.is-hero-science {
      .c-home-hero__container {
        padding-bottom: 55%;
      }
    }

    &__image-container {

      &.is-mobile {
        display: none
      }

      &.is-desktop {
        display: block
      }
    }

    &__container {
      padding-bottom: 88%;
    }

    &__btn {
      &.o-btn {
        padding: t-to-rem(13) t-to-rem(53);
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &.is-hero-science {
      .c-home-hero__container {
        padding-bottom: 58%;
      }
    }

    &__video {
      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }
    }

    &__container {
      padding-bottom: 97%;
    }

    &__content {
      margin-top: t-to-rem(254);

      .is-hero-landing-page & {
        padding-top: t-to-rem(254);
        padding-bottom: t-to-rem(144);
      }
    }

    &__title {
      color: $s-color--font-6;
      max-width: t-to-rem(604);
      margin-bottom: t-to-rem(36);
    }

    &__subtitle {
      max-width: t-to-rem(620);
      margin-bottom: t-to-rem(48);
    }

    &__scroll {
      width: 5.4375rem;
      height: 5.4375rem;

      svg {
        width: 1rem;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &.is-hero-science {
      .c-home-hero__container {
        padding-bottom: 47%;
      }
    }

    &__container {
      padding-bottom: 78%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &.is-hero-science {
      .c-home-hero__container {
        padding-bottom: 41%;
      }
    }

    &__container {
      padding-bottom: 70%;
    }

    &__title {
      max-width: t-to-rem(1120);
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &.is-hero-science {
      .c-home-hero__container {
        padding-bottom: 34%;
      }
    }

    &__container {
      padding-bottom: 59%;
    }
  }
}
